/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React, { useState, useContext } from 'react';
import SharedButton from '../shared/SharedButton';
import AppContext from '../../context/AppContext';

const SearchForActivist = () => {
  const [search, setSearch] = useState('');
  const { filterResults } = useContext(AppContext);

  return (
    <div className="d-flex">
      <input
        type="text"
        placeholder="Search for activists with name, movements, etc"
        className="search"
        name="search"
        value={search}
        onChange={(e) => setSearch(e.target.value)}
      />
      <div onClick={() => filterResults(search)} style={{ width: '8rem' }}>
        <SharedButton
          classname="btn btn-primary activist-search"
          text="Search"
        />
      </div>
    </div>
  );
};
export default SearchForActivist;
