import React from 'react';
import './App.css';
import { Route } from 'react-router-dom';
import Home from './components/Home';
import Activists from './components/activists/Activists';
import useActivists from './hooks/useActivists';
import { AppProvider } from './context/AppContext';

function App() {
  const {
    activists,
    onChange,
    newActivist,
    createActivist,
    handleShow,
    handleClose,
    show,
    clap,
    increaseClap,
    filterResults,
    uploading,
  } = useActivists();
  return (
    <AppProvider
      value={{
        activists,
        onChange,
        newActivist,
        createActivist,
        handleShow,
        handleClose,
        show,
        clap,
        increaseClap,
        filterResults,
        uploading,
      }}
    >
      <Route exact path="/" component={Home} />
      <Route exact path="/activists" component={Activists} />
    </AppProvider>
  );
}

export default App;
